.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #fff;
}
.hljs,
.hljs-subst {
  color: #000;
}
.hljs-string,
.hljs-meta,
.hljs-symbol,
.hljs-template-tag,
.hljs-template-variable,
.hljs-addition {
